import React, { useState, useEffect, useLayoutEffect, forwardRef } from "react";
import { FixedSizeList as List } from "react-window";
import memoize from "memoize-one";

import "./reset.css";
import "react-virtualized/styles.css";
import "./styles.css";
// import "./"

import Preview from "./Preview";
import SideHeader from "./SideHeader";
import Row from "./Row";
import KeyHandler from "./KeyHandler";




const createItemData = memoize((items) => ({ items }));

const PrototypeList = forwardRef((props, ref) => {
  const ITEM_SIZE = 61 + 4;

  return (
    <div>
      <List
        ref={ref}
        width={props.width}
        height={props.height}
        itemSize={ITEM_SIZE}
        itemCount={props.itemData.items.length}
        itemData={{
          items: props.itemData,
          onItemSelect: props.onItemSelect,
          selectedIndex: props.selectedIndex,
          currentRow: props.currentRow
        }}
      >
        {Row}
      </List>
    </div>
  );
});

function Prototypes(props) {
  const listRef = React.createRef();

  const buttonArrowUp = useKeyPress("ArrowUp");
  const buttonArrowDown = useKeyPress("ArrowDown");
  const buttonEnter = useKeyPress("Enter");

  const [data, setData] = useState([]);
  const [filtered, setFiltered] = useState(createItemData([]));

  const [showFeatured, setShowFeatured] = useState(false)
  const [scaleModeFill, setScaleModeFill] = useState(false)
  

  const [width, height] = useWindowSize();
  const [prototypeURL, setURL] = useState("");

  const [currentRow, setRow] = useState(0);
  const [selectedIndex, setSelectedIndex] = useState(-1);



  const prepareURL = (index) => {
    try {
      var localScaleMode = "normal"
      if (scaleModeFill) { localScaleMode = "fill" }
      const composedURL = "https://tilllur.com/d/" + filtered.items[index].u + "/index.html?button=off&logo=off&scale=" + localScaleMode
      // console.log(composedURL)
      return composedURL
    } catch { }
  };

  const prepareURL_toShare = (index) => {
    try {
      return "https://tilllur.com/d/" + filtered.items[index].u + "/index.html"
    } catch { }
  };

  

  useEffect(() => {
    const url = "https://tilllur.com/m.json";

    const fetchData = async () => {
      try {
        const response = await fetch(url);
        const json = await response.json();
        setData(json)
        setFiltered(createItemData(json))
      } catch (error) {
        console.log("error", error);
      }
    };

    fetchData();
  }, []);

  const handleInputChange = (searchQuery) => {
    if (showFeatured) {
      const newFiltered = data.filter(function (item) { return item.f === 1; });
      setFiltered(createItemData(searchPrototypes(searchQuery, newFiltered)));
    }
    else {
      setFiltered(createItemData(searchPrototypes(searchQuery, data)));
    }
    setRow(0);
  };


  const scrollToRow = (index) => {
    if (listRef.current && (buttonArrowUp || buttonArrowDown)) {
      listRef.current.scrollToItem(index);
    }
  };




  const handleEnterURL = () => {
    // setURL(prepareURL(currentRow))
    setSelectedIndex(currentRow)
  }

  const handleRow = (handleIndex) => {
    // setURL(prepareURL(handleIndex))
    setSelectedIndex(handleIndex)
  }


  const handleNextIndexChange = () => {
    var nextIndex = currentRow + 1;
    if (nextIndex >= filtered.items.length) {
      nextIndex = currentRow;
    }
    setRow(nextIndex);
  };

  const handlePrevIndexChange = () => {
    var nextIndex = currentRow - 1;
    if (nextIndex < 0) {
      nextIndex = 0;
    }
    setRow(nextIndex);
  };





  const showAllHandler = () => {
    setShowFeatured(false)
    setFiltered(createItemData(data))
  };

  const showFeaturedHandler = () => {
    setShowFeatured(true)
    const newFiltered = data.filter(function (item) { return item.f === 1; });
    setFiltered(createItemData(newFiltered))
  };

  const sharePrototypeHandler = () => {
    const sharedURL = prepareURL_toShare(selectedIndex)
    navigator.clipboard.writeText(sharedURL)
  };

  const openPrototypeHandler = () => {
    const sharedURL = prepareURL_toShare(selectedIndex)
    window.open(sharedURL, '_blank');
  };
  
  const changeScaleModeHandler = () => {
    setScaleModeFill(!scaleModeFill)
  }

  
  useEffect(() => {
    // console.log(scaleModeFill)
    try {
      if (scaleModeFill) {
        window.document.getElementById('preview-iframe-id').contentWindow.previewMessageFramerObject.animateStateToFill()
      }
      else {
        window.document.getElementById('preview-iframe-id').contentWindow.previewMessageFramerObject.animateStateToNormal()
      }
    } catch { }
    
  }, [scaleModeFill]);

  useEffect(() => {
    if (selectedIndex == -1) { setSelectedIndex(0) }
  }, [filtered]);

  useEffect(() => {
    if (selectedIndex == 0) { setURL(prepareURL(selectedIndex)) }
    else { setSelectedIndex(0) }
  }, [showFeatured]);

  useEffect(() => {
    scrollToRow(currentRow);
  }, [currentRow]);

  useEffect(() => {
    setRow(selectedIndex);
    setURL(prepareURL(selectedIndex))
  }, [selectedIndex]);



  return (
    <div className="test">
      <div className="flex-container">
        <div>
          <SideHeader
            className="searchInput"
            onInputChange={handleInputChange}
            height={240}
          />

          <PrototypeList
            ref={listRef}
            itemData={filtered}
            width={480}
            height={height - 240}
            onItemSelect={handleRow}
            selectedIndex={selectedIndex}
            currentRow={currentRow}
          />
        </div>
        <KeyHandler
          buttonArrowDown={buttonArrowDown}
          buttonArrowUp={buttonArrowUp}
          buttonEnter={buttonEnter}
          selectedIndex={selectedIndex}
          itemData={filtered}
          onNextIndexChange={handleNextIndexChange}
          onPrevIndexChange={handlePrevIndexChange}
          onEnterChange={handleEnterURL}
        />
        {/* <button
          className="ExampleButton"
          onClick={scrollToRow(selectedIndex)}
        >
          Scroll to row 2
        </button> */}
        <Preview
          url={prototypeURL}
          width={width - 480} height={height}
          onTitleAllChange={showAllHandler}
          onTitleFeaturedChange={showFeaturedHandler}
          onShareChange={sharePrototypeHandler}
          onOpenChange={openPrototypeHandler}
          isFeatured={showFeatured}
          scaleModeFill={scaleModeFill}
          onScaleModeChange={changeScaleModeHandler}
        />
        
      </div>
    </div>
  );
}

export default function App() {
  return (
    <div className="App">
      <Prototypes />
    </div>
  );
}





function useWindowSize() {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    function updateSize() {
      setSize([window.innerWidth, window.innerHeight]);
    }
    window.addEventListener("resize", updateSize);
    updateSize();
    return () => window.removeEventListener("resize", updateSize);
  }, []);
  return size;
}

function searchPrototypes(searchQuery, data) {
  const parts = searchQuery.toLowerCase().trim().split(" ");

  return data.filter(function (item) {
    const project = item.p.toLowerCase().trim();
    const title = item.t.toLowerCase().trim();
    const unitedStr = project.concat(title);

    var success = true;
    for (var index = 0; index < parts.length; index++) {
      if (!unitedStr.includes(parts[index])) {
        success = false;
      }
    }

    return success;
  });
}



const useKeyPress = function (targetKey) {
  const [keyPressed, setKeyPressed] = useState(false);

  function downHandler({ key }) {
    if (key === targetKey) {
      setKeyPressed(true);
    }
  }

  const upHandler = ({ key }) => {
    if (key === targetKey) {
      setKeyPressed(false);
    }
  };

  React.useEffect(() => {
    window.addEventListener("keydown", downHandler);
    window.addEventListener("keyup", upHandler);

    return () => {
      window.removeEventListener("keydown", downHandler);
      window.removeEventListener("keyup", upHandler);
    };
  });

  return keyPressed;
};
