import React, { useState } from "react";

import "./SideHeader.css";
import { ReactComponent as SearchIcon } from './assets/search-icon.svg';
import { ReactComponent as LogoIcon } from './assets/logo-icon.svg';

function SideHeader(props) {

  return (
    <div className="sideHeader" height={props.height}>
      <div className="logo">
        <a href="https://tilllur.com">
          <LogoIcon className="logo-svg"/>
        </a>
      </div>
      <h1 className="title">Prototypes</h1>
      <div className="search">
        <SearchIcon className="search-icon" />
        <SearchInput onInputChange={props.onInputChange} />
      </div>
      <div className="flex-grow"></div>
      <div className="search-divider"></div>
    </div>
  );
}

function SearchInput(props) {
  const [value, setValue] = useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
    props.onInputChange(event.target.value);
  };

  return (
    <div>
      <form className="form-prototypes" onSubmit={(e) => e.preventDefault()}>
        <input
          type="text"
          value={value}
          onChange={handleChange}
          className="input-prototypes"
          placeholder="Search by title or project"
        />
      </form>
    </div>
  );
}

export default SideHeader;
