import React, { useEffect } from "react";

function KeyHandler(props) {
  useEffect(() => {
    if (props.itemData.items.length && props.buttonArrowDown) {
      props.onNextIndexChange();
    }
  }, [props.buttonArrowDown]);

  useEffect(() => {
    if (props.itemData.items.length && props.buttonArrowUp) {
      props.onPrevIndexChange();
    }
  }, [props.buttonArrowUp]);

  useEffect(() => {
    if (props.itemData.items.length && props.buttonEnter) {
      props.onEnterChange();
    }
  }, [props.buttonEnter]);

  return <div className="hiddenKeyHandler"></div>;
}

export default KeyHandler;
