import React, { useState } from "react";
import Iframe from "react-iframe";

import "./Preview.css";
import { ReactComponent as FullscreenIcon } from './assets/fullscreen-icon.svg';


function Preview(props) {

  const [copyLinkText, setCopyLinkText] = useState("Share Prototype")
  
  const cna = (className) => {
    if (props.isFeatured == false) { return className + " active"; }
    return className;
  };

  const cnf = (className) => {
    if (props.isFeatured) { return className + " active"; }
    return className;
  };

  const handleAll = (event) => {
    props.onTitleAllChange();
  };

  const handleFeatured = (event) => {
    props.onTitleFeaturedChange();
  };

  const handleSharePrototype = (event) => {
    setCopyLinkText("Done ")
    setTimeout(()=>{
      setCopyLinkText("Share Prototype")
    }, 1000)
    props.onShareChange();
  };

  const handleOpenPrototype = (event) => {
    props.onOpenChange();
  };

  const handleScaleMode = (event) => {
    props.onScaleModeChange();
  };


  const cnborderInside = (className) => {
    if (props.scaleModeFill == false) { return className + " active"; }
    return className;
  };

  const cnborder = (className) => {
    if (props.scaleModeFill) { return className + " active"; }
    return className;
  };


  return (
    <div className="preview-column" style={{ height: props.height, width: props.width }}>
      <div className="preview-header" style={{ height: 56, width: props.width - 40 - 36 }}>
        <h3 className={cna("preview-title")}><a href="#" onClick={handleAll}>All</a></h3>
        <h3 className={cnf("preview-title")}><a href="#" onClick={handleFeatured}>Featured</a></h3>
        <h3 className="flex-grow"></h3>
        <h3 className="preview-share"><a href="#" onClick={handleSharePrototype}>{copyLinkText}</a></h3>
        <a href="#" onClick={handleOpenPrototype}><FullscreenIcon className="fullscreen-icon"/></a>
      </div>
      <div className="preview-content"
        style={{ height: props.height - 56, width: props.width }}
      >
        <Iframe
          url={props.url}
          id="preview-iframe-id" className="preview-frame"
          width={props.width - 32} height={props.height - 56 - 16 - 4}
        />
        <a href="#" onClick={handleScaleMode}>
          <div className={cnborder("scale-button")}>
            <div className={cnborderInside("scale-button-inside")}></div>
          </div>
        </a>
      </div>
    </div>
  );
}

export default Preview;

