import React, { memo } from "react";
import { areEqual } from "react-window";

import "./Row.css";
import { ReactComponent as ItemIcon } from './assets/item-icon.svg';
import { ReactComponent as ItemIconPublished } from './assets/item-icon-published.svg';
import { ReactComponent as ItemFavIcon } from './assets/item-fav-icon.svg';

const Row = memo(({ data, index, style }) => {
  const { items } = data.items;
  const item = items[index];
  const selectedIndex = data.selectedIndex;
  const currentRow = data.currentRow;

  const handleClick = (event) => {
    data.onItemSelect(index);
  };

  const cn = (className, i) => {
    if (i === currentRow) {
      return className + " active";
    }
    return className;
  };

//   const getIcon = (isFeatured, index) => {
//     if (isFeatured) { return <ItemIcon className={cn("row-icon", index)} /> }
//     else { return <ItemFavIcon className={cn("row-icon", index)} /> }
//   };

  return (
    <div
      className={cn("prototypeItem", index)}
      style={style}
      onClick={handleClick}
    >
      <div className={cn("row-bg", index)}>
        { item.f == 1?
			<ItemFavIcon className={cn("row-fav-icon", index)} />
          	:
		  	item.s == 1?
          		<ItemIconPublished className={cn("row-published-icon", index)} />
				:
				<ItemIcon className={cn("row-icon", index)} />
        }
        <div className="row-content">
          <div className="row-line-top">
            <h3 className={cn("row-title", index)}>{item.t}</h3>
            <h3 className={cn("row-index", index)}>{item.i}</h3>
          </div>
          <div className="row-line-bottom">
            <h4 className="row-project">{item.p}</h4>
            <h4 className="row-year">{item.y}</h4>
          </div>
        </div>
      </div>
    </div>
  );
}, areEqual);

export default Row;
